@media (max-width: 1200px) {
   .menu {
      &_item {
         padding-right: 20px; }
      &_link {
         font-size: 13px; } }
   .promo {
      .promo_title {
         font-size: 50px; } }
   .mobile_item {
      width: 300px; }
   .footer_wrapper {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      div:first-child {
         order: 2; }
      div:nth-child(2) {
         order: 1; }
      div:last-child {
         order: 3; } }
   .footer_wrapper > div {
      width: 100%;
      display: flex;
      justify-content: center;
      margin-bottom: 20px;
      &:nth-child(3) {
         width: 100%;
         justify-content: center; } } }

@media (max-width: 991px) {
   .menu {
      &_item {
         padding-right: 8px; }
      &_link {
         font-size: 10px; } }
   .subheader {
      &_logo {
         img {
            width: 100%; } }
      &_official {
         font-size: 12px; }
      &_phone {
         font-size: 21px; }
      &_btn {
         width: 157px;
         height: 40px;
         margin-top: 22px; } }
   .promo {
      .promo_title {
         font-size: 40px; }
      &_subtitle {
         font-size: 30px; } }
   .reasons {
      .item {
         flex-direction: column;
         align-items: center;
         &_round {
            width: 116px; }
         &_block {
            margin-top: 20px; } } }
   .choice {
      min-height: 854px;
      &_img {
         width: 700px;
         height: 440px; }
      .light_bg {
         transform: translateY(400px); } } }

@media (max-width: 767px) {
   .menu {
      position: fixed;
      top: 0;
      left: -100%;
      flex-direction: column;
      width: 50%;
      height: 100vh;
      padding: 20px 15px;
      background-color: rgba(0,0,0,.8);
      justify-content: center;
      transition: 0.6s all;
      &_active {
         left: 0; }
      &_item {
         margin: 10px 0;
         padding-right: 0;
         border: none; }
      &_link {
         font-size: 20px; } }
   .hamburger {
      display: block;
      position: absolute;
      top: 50%;
      right: 10px;
      transform: translateY(-50%);
      width: 26px;
      height: 12px;
      span {
         display: block;
         width: 100%;
         height: 2px;
         background-color: #fff;
         margin-top: 2px;
         transition: 0.5s all; }
      &_active {
         span {
            margin-top: -2px;
            &:nth-child(1) {
               transform: translateY(3px) rotate(-45deg); }
            &:nth-child(2) {
               display: none; }
            &:nth-child(3) {
               transform: translateY(3px) rotate(45deg); } } } }
   .xs-hidden {
      display: none; }
   .xs-visible {
      display: block;
      text-align: center;
      font-weight: 400;
      font-size: 16px;
      text-transform: uppercase;
      color: #FFFFFF;
      margin-top: 5px; }
   .subheader_btn {
      display: block;
      margin: 0 auto;
      width: 200px;
      height: 40px;
      margin-top: 5px; }

   .promo {
      .promo_title {
         line-height: 46px; } }

   .reasons {
      .item_block {
         .item_title {
            text-align: center; } }
      .item_round {
         margin-right: 0; } }
   .mobile {
      .title {
         font-size: 30px; } }
   .mobile_item {
      width: 100%;
      height: 150px;
      .item_header {
         padding-top: 30px; }
      .item_plus {
         bottom: 30px; } }
   .choice {
      min-height: 754px;
      .title {
         font-size: 30px; }
      &_img {
         width: 500px;
         height: 360px; }
      .light_bg {
         transform: translateY(300px); } }
   .location {
      min-height: 500px; } }

@media (max-width: 575px) {
   .subheader {
      height: 62px;
      transform: translateY(32px);
      &_official {
         display: none; }
      &_phone {
         font-size: 12px; }
      &_btn {
         width: 110px;
         height: 22px;
         font-size: 10px; } }
   .menu {
      width: 85%; }
   nav {
      height: 32px; }
   .promo {
      min-height: 400px;
      padding: 135px 0 54px 0;
      .promo_title {
         font-size: 30px;
         line-height: 35px;
         padding: 0 13px; }
      &_subtitle {
         font-size: 20px;
         line-height: 23px; }
      &_text {
         display: none; }
      &_btn {
         width: 210px;
         height: 50px;
         font-size: 16px;
         margin-top: 73px; } }
   .reasons {
      padding: 23px 0 38px 0;
      .item {
         margin-top: 24px; }
      .title {
         font-size: 16px;
         margin-top: 16px; } }
   .mobile {
      .title {
         font-size: 16px; }
      .subtitle {
         font-size: 14px;
         margin-top: 9px; } }
   .mobile_item {
      width: 290px;
      height: 254px;
      margin: 0 auto;
      margin-top: 30px;
      .item_header {
         padding: 80px 33px 0 31px;
         font-size: 18px; } }
   .mobile_item_1 {
      margin-top: 0; }
   .choice {
      min-height: 393px;
      padding-top: 26px;
      .light_bg {
         display: none; }
      .title {
         display: none; }
      .subtitle {
         display: none; }
      &_img {
         width: 300px;
         height: 190px;
         margin-top: 36px; }
      &_text {
         font-size: 17px;
         color: #fff;
         margin-top: 23px; } }
   .require {
      padding: 22px 0 30px 0;
      .title {
         font-size: 16px;
         margin-top: 16px; }
      .require_block {
         margin-top: 30px;
         .require_title {
            font-size: 16px; }
         .require_list {
            margin-top: 5px;
            li {
               font-size: 14px; } } } }
   .location {
      padding: 26px 0 22px 0;
      min-height: 360px;
      .title {
         font-size: 16px;
         margin-top: 14px; }
      .location_subtitle {
         font-size: 14px;
         margin-top: 11px; }
      .location_link {
         font-size: 15px;
         width: 100%; } }

   .footer_links {
      width: 290px;
      &_main {
         padding: 0;
         font-size: 14px;
         justify-content: center;
         align-items: center;
         flex-wrap: wrap;
         a {
            padding: 5px; } }
      &_sub {
         flex-wrap: wrap;
         justify-content: center;
         a {
            margin: 0 7px; } }
      &_lang {
         margin-top: 10px; } } }






