header {
	position: relative; }
nav {
	position: fixed;
	width: 100%;
	height: 53px;
	background: #212121;
	z-index: 5; }

.hamburger {
   display: none; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 14px 70px;
	margin: 0; }
.menu_link {
	font-style: normal;
	font-weight: 300;
	font-size: 15px;
	color: #FFFFFF;
	text-decoration: none; }
.menu_item {
	border-right: 2px dotted black;
	padding-right: 28px; }
.subheader {
	position: absolute;
	width: 100%;
	height: 87px;
	background-color: rgba(0, 0, 0, .6);
	transform: translateY(53px); }
.subheader_logo {
	display: block;
	max-width: 100%;
	margin-top: 19px; }
.subheader_official {
	font-family: 'Roboto';
	font-style: normal;
	font-weight: 100;
	font-size: 13px;
	color: #FFFFFF;
	opacity: 0.6; }
.subheader_call {
	margin-top: 19px;
	font-style: normal;
	font-weight: 300;
	font-size: 12px;
	text-align: right;
	color: #FFFFFF; }
.subheader_phone {
	display: block;
	font-weight: 400;
	font-size: 22px;
	text-align: right;
	text-transform: uppercase;
	color: #FFFFFF;
	text-decoration: none; }
.subheader_btn {
	margin-top: 19px;
	width: 172px;
	height: 46px;
	background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
	box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
	border-radius: 4px;
	font-family: 'Roboto';
	font-style: normal;
	font-weight: 400;
	font-size: 14px;
	text-align: center;
	text-transform: uppercase;
	color: #FFFFFF;
	border: none; }
.promo {
	min-height: 800px;
	background: url('../img/bg.jpg') center center/cover no-repeat;
	padding: 208px 0 186px 0; }
.promo_title {
	font-style: normal;
	font-weight: 900;
	font-size: 60px;
	text-align: center;
	text-transform: uppercase;
	color: #FFFFFF;
	line-height: 52px;
	margin: 0; }
.promo_subtitle {
	margin: 0;
	font-style: normal;
	font-weight: 700;
	font-size: 36px;
	text-align: center;
	text-transform: uppercase;
	color: #FFFFFF;
	line-height: 36px;
	margin-top: 20px; }
.promo_text {
	margin-top: 20px;
	font-style: normal;
	font-weight: 400;
	font-size: 18px;
	line-height: 24px;
	text-align: center;
	color: #FFFFFF; }
.promo_btn {
	display: block;
	margin: 116px auto 0 auto;
	width: 259px;
	height: 63px;
	background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
	box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
	border-radius: 4px;
	font-weight: 400;
	font-size: 18px;
	text-transform: uppercase;
	color: #FFFFFF;
	text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
	border: none; }
.reasons {
	padding: 18px 0 64px 0; }
.item {
	display: flex;
	padding-left: 21px;
	margin-top: 30px;
	&:nth-child(2n) {
		padding-right: 31px; } }
.advantage {
	width: 115px;
	height: 27px;
	background: #000000;
	margin: 0 auto;
	font-weight: 400;
	font-size: 11px;
	text-align: center;
	text-transform: uppercase;
	color: #FFFFFF;
	line-height: 27px; }
.title {
	margin-top: 32px;
	margin-bottom: 16px;
	font-weight: 700;
	font-size: 38px;
	text-align: center;
	text-transform: uppercase;
	color: #222222; }
.item_round {
	min-width: 116px;
	height: 116px;
	background: #1EACC7;
	border-radius: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-right: 25px;
	img {
		max-width: 80px;
		max-height: 66px; } }
.item_title {
	font-weight: 400;
	font-size: 17px;
	line-height: 21px;
	color: #1EACC7; }
.item_text {
	margin: 0;
	font-weight: 300;
	font-size: 15px;
	line-height: 21px;
	color: #202020; }

.mobile {
	min-height: 864px;
	background: url('../img/mobile_bg.png') center center/cover no-repeat;
	padding: 48px 0 80px 0;
	.row {
		margin-top: 30px; }
	.title {
		margin-bottom: 0; } }

.subtitle {
	font-weight: 400;
	font-size: 17px;
	text-align: center;
	color: #222222;
	margin: 0 auto; }

.mobile_item {
	position: relative;
	width: 320px;
	height: 280px;
	margin-bottom: 19px;
	&_1 {
		background: url('../img/card_1.jpg') center center/cover no-repeat; }
	&_2 {
		background: url('../img/card_2.jpg') center center/cover no-repeat; }
	&_3 {
		background: url('../img/card_3.jpg') center center/cover no-repeat; }
	&_4 {
		background: url('../img/card_4.jpg') center center/cover no-repeat; }
	&_5 {
		background: url('../img/card_5.jpg') center center/cover no-repeat; }
	&_6 {
		background: url('../img/card_6.jpg') center center/cover no-repeat; }
	.item_header {
		padding-top: 89px;
		font-weight: 400;
		font-size: 24px;
		line-height: 31px;
		text-transform: uppercase;
		color: #FFFFFF;
		text-align: center; }
	.item_plus {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		bottom: 41px;
		left: 50%;
		transform: translateX(-50%);
		width: 29px;
		height: 29px;
		background: #000000;
		border-radius: 100%;
		cursor: pointer; } }

.choice {
	position: relative;
	min-height: 896px;
	background: #070716;
	// background: url('../img/mobile_bg.png') center center/cover no-repeat
	padding: 57px 0 0 0;
	z-index: -1;
	.advantage {
		background: #FFFFFF;
		color: #070716;
		cursor: pointer; }
	.title {
		color: #FFFFFF;
		margin-bottom: 0; }
	.subtitle {
		color: #FFFFFF; }
	&_img {
		position: relative;
		display: block;
		margin: 54px auto 0 auto;
		z-index: 2; }
	&_text {
		position: relative;
		text-align: center;
		font-weight: 300;
		font-size: 17px;
		line-height: 24px;
		color: #222222;
		margin: 0 auto;
		z-index: 2; }
	.light_bg {
		position: absolute;
		width: 100%;
		min-height: 397px;
		background: url('../img/mobile_bg.png') center center/cover no-repeat;
		transform: translateY(442px);
		z-index: 1; } }

.require {
	padding: 68px 0 59px 0;
	.title {
		margin-bottom: 0; }
	.require_block {
		margin-top: 33px;
		margin-bottom: 28px;
		.require_title {
			font-weight: 400;
			font-size: 22px;
			line-height: 24px;
			color: #1CA8C3; }
		.require_list {
			margin-top: 20px;
			list-style-type: none;
			padding-left: 50px;
			li {
				position: relative;
				font-weight: 300;
				font-size: 17px;
				line-height: 24px;
				color: #222222;
				&::before {
					position: absolute;
					content: '';
					display: inline-block;
					width: 21px;
					height: 20px;
					background: url('../icons/check.png') center center/cover no-repeat;
					left: -32px;
					top: 3px; } } } }
	.warning {
		.require_title {
			color: #FF5656; }
		.require_list {
			li {
				&::before {
					background: url('../icons/check_red.png') center center/cover no-repeat; } } } } }

.location {
	position: relative;
	padding: 48px 0 22px 0;
	min-height: 716px;
	background: url('../img/world_bg.jpg') center center/cover no-repeat;
	.advantage {
		width: 79px;
		height: 27px;
		background: #FFFFFF;
		color: #070716; }
	.title {
		color: #FFFFFF;
		margin-bottom: 0; }
	.location_subtitle {
		font-weight: 400;
		font-size: 17px;
		text-align: center;
		color: #FFFFFF;
		margin-top: 2px; }
	.location_link {
		display: block;
		position: absolute;
		font-weight: 400;
		font-size: 15px;
		line-height: 68px;
		text-align: center;
		text-transform: uppercase;
		color: #1FBAD6;
		text-decoration: none;
		left: 50%;
		bottom: 0;
		transform: translateX(-50%); } }


footer {
	padding: 14px 23px 39px 23px;
	a {
		text-decoration: none; } }

.footer {
	&_divider {
		width: 299px;
		height: 4px;
		background-color: #1fbad6;
		margin: 0 auto; }
	&_wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px; }
	&_wrapper > div {
		width: 33%;
		&:nth-child(3) {
			display: flex;
			justify-content: flex-end; } }
	&_social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		&_item {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 4px;
			width: 32px;
			height: 32px;
			background-color: #070716;
			border-radius: 100%; } }
	&_links {
		width: 390px;
		margin: 0 auto;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				color: #070716;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_sub {
			display: flex;
			justify-content: space-between;
			margin-top: 10px;
			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_lang {
			display: block;
			margin: 0 auto;
			color: #1fbad6;
			font-size: 11px;
			font-weight: 400;
			margin-top: 19px;
			text-transform: uppercase;
			text-align: center; } } }

.xs-visible {
	display: none; }

._icon-s-insta:before {
	content: "\e903";
	color: white; }

._icon-s-linkedin:before {
	content: "\e904";
	color: white; }

._icon-s-twitter:before {
	content: "\e905";
	color: white; }

